import React from 'react';
import { Link } from 'react-router-dom';
import { Formik, Form } from 'formik';
import * as yup from 'yup';
import djangoAPI from '../api/djangoAPI';
import history from '../utils/history';

import { useStyles } from './login_register-style';
import { Button, Typography, Grid, Paper } from '@material-ui/core';
import Input from '../components/form_controls/input/Input';
// import GoogleeLogin from '../components/google_login/GoogleLogin';
import FormAvatar from '../components/form_controls/avatar/FormAvatar';

const INITIAL_VALUES = {
  username: '',
  firstName: '',
  lastName: '',
  email: '',
  password: '',
  confirmPassword: '',
};

const PASSWORD_CONFIG = {
  lowercase: '^(?=.*[a-z])',
  uppercase: '^(?=.*[A-Z])',
  number: '^(?=.*[0-9])',
};

const REGISTER_VALIDATION = yup.object({
  username: yup.string().required('Korisniƒçko ime je obavezno'),
  firstName: yup
    .string()
    .min(3, ({ min }) => `Ime mora sadr≈æati barem ${min} slova`)
    .max(
      15,
      ({ max, value }) =>
        `Ime ne mo≈æe prelaziti ${max} znakova, unijeli ste ${
          value.length - max
        } vi≈°e`
    )
    .required('Unesite ime'),
  lastName: yup
    .string()
    .max(
      30,
      ({ max, value }) =>
        `Prezime ne mo≈æe imati preko ${max} znakova, unijeli ste ${
          value.length - max
        } vi≈°e`
    )
    .required('Unesite prezime'),
  email: yup
    .string()
    .lowercase('Email unesite malim slovima')
    .email('Email mora biti validan')
    .required('Email je obavezan'),
  password: yup
    .string()
    .matches(PASSWORD_CONFIG.lowercase, '≈†ira mora imati malo slovo')
    .matches(PASSWORD_CONFIG.uppercase, '≈†ifra mora imati veliko slovo')
    .matches(PASSWORD_CONFIG.number, '≈†ifra mora imati broj')
    .matches(
      '^(?=.*[@$!%*#?&])',
      '≈†ifra mora imati specijalni karakter (@$!%*#?&)'
    )
    .min(
      8,
      ({ min, value }) =>
        `≈†ifa mora imati minimum 8 znakova, jo≈° ${min - value.length} `
    )
    .max(
      16,
      ({ max, value }) =>
        `≈†ifra mo≈æe imati najvi≈°e 16 znakova, unijeli ste ${
          value.length - max
        } vi≈°e`
    )
    .required('≈†ifra je obavezna'),
  confirmPassword: yup
    .string()
    .oneOf([yup.ref('password'), ''], '≈†ifre se ne podudaraju')
    .required('Morate potvrditi ≈°ifru'),
});

function RegisterPage({ setValue }) {
  const classes = useStyles();

  const onSubmit = async (values, onSubmitProps) => {
    onSubmitProps.setSubmitting(false);
    onSubmitProps.resetForm();
    try {
      const response = await djangoAPI.post('/createModerator/', {
        username: values.username,
        password: values.password,
        first_name: values.firstName,
        last_name: values.lastName,
        email: values.email,
      });
      if (response.status === 201) {
        setValue(0);
        history.push('/');
      }
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <Grid>
      <Paper elevation={10} className={classes.paperRoot}>
        <Grid align="center">
          <FormAvatar icon="circle" className={classes.formAvatar} />

          <Typography variant="h4" component="h2" className={classes.formTitle}>
            REGISTRUJ SE
          </Typography>
        </Grid>

        <Formik
          initialValues={INITIAL_VALUES}
          validationSchema={REGISTER_VALIDATION}
          onSubmit={onSubmit}
        >
          {(formik) => {
            return (
              <Form>
                <Input
                  type="text"
                  label="Korisniƒçko ime"
                  name="username"
                  icon="user"
                  placeholder="kimosabe13"
                />

                <Input
                  type="text"
                  label="Ime"
                  name="firstName"
                  icon="face"
                  placeholder="Ljubi≈°a"
                />

                <Input
                  type="text"
                  label="Prezime"
                  name="lastName"
                  icon="face"
                  placeholder="Doe"
                />

                <Input
                  type="email"
                  label="Email"
                  name="email"
                  icon="email"
                  placeholder="test@mail.com"
                />

                <Input
                  type="password"
                  label="≈†ifra"
                  name="password"
                  icon="lock"
                />

                <Input
                  type="password"
                  label="Potvrdite ≈°ifru"
                  name="confirmPassword"
                  icon="verify"
                />

                <Button
                  type="submit"
                  disabled={!formik.isValid}
                  component="button"
                  variant="contained"
                  fullWidth
                  className={classes.submitBtn}
                >
                  POTVRDI
                </Button>
              </Form>
            );
          }}
        </Formik>

        <Typography className={classes.formRedirect}>
          Ima≈° nalog{' '}
          <Link to="/login" onClick={() => setValue(2)}>
            Prijavi se
          </Link>
        </Typography>

        {/* <GoogleeLogin /> */}
      </Paper>
    </Grid>
  );
}

export default RegisterPage;
